"use client";
import { useEffect, useState } from "react";
import { Button } from "@/components/ui/button";
import axios from "axios";
import { motion } from "framer-motion";

export default function HeroSection() {
  const [message, setMessage] = useState("");

  useEffect(() => {
    axios.get("http://localhost:8000/api/hello/").then((res) => {
      setMessage(res.data.message);
    });
  }, []);

  return (
    <section className="w-full flex flex-col items-center justify-center bg-gradient-to-r from-[#f0f6ff] to-[#a7c7ff] px-6 py-16 gap-12 text-center relative overflow-hidden border-b border-blue-100">
      <motion.div
        initial={{ opacity: 0, y: 40 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.7 }}
        className="w-full relative z-10"
      >
        <h1 className="text-4xl sm:text-5xl lg:text-6xl font-extrabold text-gray-800 mb-6 leading-tight drop-shadow-[0_8px_40px_rgba(44,0,80,0.15)]">
          Welcome to Braselton Tech
        </h1>
        <p className="text-lg sm:text-xl text-gray-800 mb-8 max-w-xl mx-auto drop-shadow-[0_4px_20px_rgba(44,0,80,0.10)]">
          Join a vibrant tech ecosystem. Connect, pitch, and grow with Braselton Tech.
        </p>
        <Button className="bg-white text-gray-800 font-mono font-extrabold tracking-wide uppercase px-8 py-4 rounded-md text-lg shadow-lg border border-blue-100 hover:bg-blue-50 transition">
          Get Started
        </Button>
      </motion.div>
    </section>
  );
} 